import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'double-eleven',
    component: () => import('@/views/double-eleven'),
    meta: {
      title: ''
    }
  },
  {
    path: '/double-eleven-welfare',
    name: 'double-eleven-welfare',
    component: () => import(/* webpackChunkName: "" */ '../views/double-eleven-welfare.vue'),
    meta: {
      title: ''
    }
  },
  {
    path: '/question',
    name: 'question',
    component: () => import(/* webpackChunkName: "" */ '../views/question.vue'),
    meta: {
      title: ''
    }
  }
]
const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router
